/* panel style */
::selection {
  background: #fff7dd;
}
* {
  outline: none;
  box-sizing: border-box;
}
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "Microsoft YaHei";
  color: #4C5764;
  font-size: 14px;
  overflow: hidden;
  background-color: #EFEFEF;
}
a {
  color: inherit;
  text-decoration: none;
}
a:hover, a:focus, a:visited, a:active, a.active {
    color: inherit;
    text-decoration: none;
    outline: none;
}
ul {
  margin: 0;
  padding: 0;
}
ul li {
    list-style: none;
    list-style-type: none;
}
.container {
  width: 100%;
  height: 100%;
  padding: 0;
}
.content {
  position: relative;
  height: 100%;
}
.content .contentMain {
    overflow: scroll;
    margin-left: 183px;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    position: relative;
    background-color: #EFEFEF;
    margin-top: 60px;
}
.content .contentMain.noSiderBar {
      margin-left: 0;
      margin-top: 110px;
      background-color: #fff;
}
.content .contentMain.noSiderBar > .main {
        min-width: 1280px;
}
.content .contentMain.noSiderBar.noActionBar {
        margin-top: 60px;
}
.content .contentMain.noSiderBar.noTabBar {
        margin-top: 60px;
}
.content .contentMain > .main {
      width: 100%;
      height: 100%;
      min-width: 1097px;
}
.mask {
  background: rgba(235, 239, 241, 0.76);
}
.ellipsis {
  max-width: 200px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  -webkit-text-overflow: ellipsis;
  -moz-text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: bottom;
}

/* el-row */
.el-row {
  padding: 15px 24px;
}
.el-row.row-actionBar {
    background-color: #FFFFFF;
    box-shadow: 0 2px 8px 0 rgba(13, 29, 74, 0.19);
    position: fixed;
    width: 100%;
    top: 60px;
    height: 50px;
    padding: 0;
    line-height: 48px;
    text-align: right;
    z-index: 3000;
}
.el-row.row-tabBar {
    background: #FFFFFF;
    box-shadow: 0 2px 8px 0 rgba(14, 29, 74, 0.44);
    position: fixed;
    width: 100%;
    bottom: 0;
    height: 50px;
    padding: 0;
    line-height: 48px;
    text-align: right;
    z-index: 3000;
}
.el-row.row-adaptHeight {
    height: 100%;
}
.el-row.row-adaptHeight .el-col {
      height: 100%;
}
.el-row.gutter-2 .el-col:nth-child(1) {
    padding-right: 10px;
}
.el-row.gutter-2 .el-col:nth-child(2) {
    padding-left: 10px;
}
.el-row .el-col .col-title {
    font-size: 16px;
    color: #4C5764;
    margin-bottom: 10px;
    font-weight: bold;
    position: relative;
}
.el-row .el-col .col-title.admin-title {
      font-size: 18px;
      color: #4A4A4A;
}
.el-row .el-col .col-title span {
      display: inline-block;
      position: relative;
      left: 0;
      top: 0;
}
.el-row .el-col .col-title .detail {
      height: 16px;
      line-height: 16px;
      display: inline-block;
      position: absolute;
      font-size: 12px;
      color: #4B61D9;
      border-bottom: 1px solid #4B61D9;
      right: 24px;
      top: 0;
}

/* panel style */
.panel {
  padding: 16px;
  background: #FFF;
  box-shadow: 0 1px 18px 0 rgba(0, 0, 0, 0.06);
  border-radius: 2px;
}
.ps-container .ps-scrollbar-x {
  display: none;
}
.ps-container .ps-scrollbar-y {
  background-color: #D5E0E4;
}
.hidden {
  display: none;
}
.clearfix {
  clear: both;
  overflow: hidden;
}
.wait-chartload {
  height: 100%;
  text-align: center;
  padding-top: 150px;
  position: relative;
}

/* dateRangeTip start */
.modal-dialog.dialog-dateRange {
  font-size: 14px;
}
.modal-dialog.dialog-dateRange .modal-header {
    background-color: #3F51B5;
    color: #fff;
}
.modal-dialog.dialog-dateRange .modal-header .close {
      color: #fff;
      opacity: .7;
}
.modal-dialog.dialog-dateRange .modal-header .close:hover, .modal-dialog.dialog-dateRange .modal-header .close:active {
        opacity: 1;
}
.modal-dialog.dialog-dateRange .md-content {
    padding: 25px 36px 0;
}
.modal-dialog.dialog-dateRange .modal-footer {
    border-top: none;
    text-align: center;
    padding: 0px 20px 20px;
}
.modal-dialog.dialog-dateRange .modal-footer .btn-action {
      padding: 6px 32px;
      background-color: #3a60ff;
      color: #fff;
}
.modal-dialog.dialog-dateRange .modal-footer .btn-action:hover, .modal-dialog.dialog-dateRange .modal-footer .btn-action:active {
        background-color: #324195;
        outline: none;
}

/* dateRangeTip end */
/* ps-scrollbar-y */
.ps-container:hover .ps-scrollbar-y-rail, .ps-container.hover .ps-scrollbar-y-rail {
  opacity: 1;
}

/*隐藏元素 用来计算文字的px宽度/高度*/
.whPx {
  visibility: hidden;
  white-space: nowrap;
  display: inline-block;
  position: absolute;
  top: -2000px;
}
.whPx.whPx.width20px {
    font-size: 20px;
}
.whPx.width18px {
    font-size: 18px;
}
.whPx.height12px {
    font-size: 12px;
    max-width: 230px;
    text-align: left;
    border-radius: 2px;
    padding: 6px 16px;
    white-space: normal;
}
.fixedBtn {
  position: fixed;
  right: 18px;
  bottom: 104px;
  z-index: 1000;
}
.feedback {
  font-size: 14px;
  color: #FFF;
  background: rgba(162, 179, 192, 0.6);
  width: 50px;
  height: 50px;
  display: block;
  cursor: pointer;
  border-radius: 100px;
}
.feedback i.icon-f-feedback {
    top: 18px;
    right: 15px;
    position: absolute;
    font-size: 16px;
}
.feedback i.icon-f-feedback:before {
      color: #fff;
}
.feedback span {
    color: #fff;
    margin-left: 26px;
    opacity: 0;
    visibility: hidden;
    transition: opacity width, 0.1s 0.1, ease ease, 0.1s 0.1s;
    -webkit-transition: opacity width, 0.1s 0.1, ease ease, 0.1s 0.1s;
    margin-bottom: -8888px;
    display: inline-block;
    height: 100%;
    line-height: 50px;
}
.feedback:hover {
    width: 140px;
    background-color: #A2B3C0;
}
.feedback:hover span {
      opacity: 1;
      visibility: visible;
      margin-bottom: auto;
}
.toTop {
  background: rgba(162, 179, 192, 0.6);
  width: 50px;
  height: 50px;
  display: none;
  margin-top: 24px;
  border-radius: 100px;
  cursor: pointer;
  display: none;
  position: absolute;
  right: 0;
}
.toTop i.icon-f-toTop {
    top: 15px;
    left: 15px;
    position: absolute;
}
.toTop i.icon-f-toTop:before {
      color: #fff;
      font-size: 21px;
}
.toTop:hover {
    background-color: #A2B3C0;
}
.i-listword {
  color: #002728;
  font-weight: 700;
}
span.icon.icon-upArrow, span.icon.icon-downArrow, span.icon.icon-rightArrow, span.icon.icon-leftArrow {
  content: '';
  display: inline-block;
  width: 7px;
  height: 7px;
  border-top: 1px solid #6B7C83;
  border-right: 1px solid #6B7C83;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
span.icon.icon-rightArrow {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
span.icon.icon-leftArrow {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}
span.icon.icon-downArrow {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}
@font-face {
  font-family: 'iconFont';
  src: url(./assets/css/sass/iconFont/icomoon.eot);
  src: url(./assets/css/sass/iconFont/icomoon.eot) format("embedded-opentype"), url(./assets/css/sass/iconFont/icomoon.ttf) format("truetype"), url(./assets/css/sass/iconFont/icomoon.woff) format("woff"), url(./assets/css/sass/iconFont/icomoon.svg) format("svg");
  font-weight: normal;
  font-style: normal;
}
i[class^="icon-f-"],
i[class*="icon-f-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'iconFont' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* transform =========== 覆盖图片图标2倍图缩放样式 */
  transform: scale(1);
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  display: inline-block;
}
