i.icon-f-down {
  transform: scale(0.3);
  -webkit-transform: scale(0.3);
  -moz-transform: scale(0.3);
  -ms-transform: scale(0.3);
  -o-transform: scale(0.3);
}
i.icon-f-up {
  transform: scale(0.3) rotate(180deg);
  -webkit-transform: scale(0.3) rotate(180deg);
  -moz-transform: scale(0.3) rotate(180deg);
  -ms-transform: scale(0.3) rotate(180deg);
  -o-transform: scale(0.3) rotate(180deg);
}
i.icon-f-right {
  transform: scale(0.3) rotate(270deg);
  -webkit-transform: scale(0.3) rotate(270deg);
  -moz-transform: scale(0.3) rotate(270deg);
  -ms-transform: scale(0.3) rotate(270deg);
  -o-transform: scale(0.3) rotate(270deg);
}
i.icon-f-left {
  transform: scale(0.3) rotate(90deg);
  -webkit-transform: scale(0.3) rotate(90deg);
  -moz-transform: scale(0.3) rotate(90deg);
  -ms-transform: scale(0.3) rotate(90deg);
  -o-transform: scale(0.3) rotate(90deg);
}
