.slide-enter-active {
  animation: slide .2s;
}
@keyframes slide {
0% {
    width: 0;
    height: 0;
    overflow: hidden;
}
50% {
    width: 50%;
    height: 50%;
    overflow: hidden;
}
75% {
    width: 75%;
    height: 75%;
    overflow: hidden;
}
99% {
    width: 100%;
    height: 100%;
    overflow: hidden;
}
100% {
    width: 100%;
    height: 100%;
    overflow: scroll;
}
}
.slide {
  -webkit-animation-name: slide;
  animation-name: slide;
}
.main {
  padding: 30px 15px;
}
.main .title-wrap .title {
