    font-size: 30px;
}
.main .content-wrap p {
    text-indent: 2em;
    font-size: 24px;
}
.main .content-wrap p .red {
      color: red;
}
.main .content-wrap pre {
    background: #000;
    padding: 15px 30px;
    color: #FFF;
    font-size: 24px;
    font-family: Microsoft YaHei;
    margin: 0 40px;
}



