.topBar {
  top: 0;
  height: 60px;
  width: 100%;
  min-width: 1024px;
  position: fixed;
  z-index: 5000;
  background-color: #FFF;
  color: #DFE4E8;
  -webkit-box-shadow: 0 2px 8px 0 rgba(13, 29, 74, 0.19);
  -moz-box-shadow: 0 2px 8px 0 rgba(13, 29, 74, 0.19);
  box-shadow: 0 2px 8px 0 rgba(13, 29, 74, 0.19);
}
.topBar .logo {
    width: 183px;
    height: 60px;
    background: #4fc08d;
    box-shadow: 0 0 4px 0 #4B61D9;
    position: absolute;
    left: 0;
    top: 0;
}
.topBar .logo i.icon-f-logo {
      position: absolute;
      left: 18px;
      top: 14px;
      width: 30px;
      height: 30px;
      color: #fff;
      font-size: 32px;
}
.topBar .logo i.icon-f-logo:before {
        color: #fff;
}
.topBar .logo .logo-title {
      width: 100px;
      font-size: 18px;
      color: #FFFFFF;
      margin: 18px 41px 20px 70px;
}
.topBar .checkBtn {
    position: absolute;
    right: 160px;
    top: 10px;
    width: 128px;
}
.topBar .line {
    position: absolute;
    width: 2px;
    height: 30px;
    background-color: #A2B3C0;
    right: 46px;
    top: 14px;
}
.topBar i.icon-f-logout {
    position: absolute;
    right: 16px;
    top: 20px;
}
.topBar i.icon-f-logout:before {
      color: #F2480B;
      font-size: 16px;
}



